import classNames from 'classnames';
import { Button } from 'ether-ui';
import { EditIcon, TrashIcon } from 'ether-ui/icons';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Integration } from '../../../types/integration';
import env from '../../common/env';
import { useAuthStore } from '../../services/auth';
import { useIntegrationStore } from '../../services/integration';

interface IntegrationCardProps {
    integration: Integration;
}

export const IntegrationCard: React.FC<IntegrationCardProps> = ({ integration }) => {
    const navigate = useNavigate();
    const { user } = useAuthStore();
    const { deleteIntegration, testIntegration } = useIntegrationStore();
    const [copiedWebhook, setCopiedWebhook] = useState(false);

    const webhookUrl =
        (integration.type === 'gupshup' || integration.type === 'whatsapp') && integration.id && user?.organizationId
            ? `${env.apiExternalBasePath}/api/webhooks/${integration.type}/${user.organizationId}/${integration.id}/callback`
            : '';

    const handleCopyWebhook = async () => {
        if (webhookUrl) {
            await navigator.clipboard.writeText(webhookUrl);
            setCopiedWebhook(true);
            setTimeout(() => setCopiedWebhook(false), 2000);
        }
    };

    const handleTest = async () => {
        const success = await testIntegration(integration.id);
        if (success) {
            alert('Integration test successful!');
        } else {
            alert('Integration test failed. Please check your configuration.');
        }
    };

    const handleDelete = async () => {
        if (window.confirm('Are you sure you want to delete this integration?')) {
            await deleteIntegration(integration.id);
        }
    };

    const getIntegrationColor = (type: string) => {
        switch (type) {
            case 'email':
                return 'blue';
            case 'gupshup':
                return 'green';
            case 'whatsapp':
                return 'green';
            case 'sms':
                return 'purple';
            case 'telegram':
                return 'cyan';
            default:
                return 'gray';
        }
    };

    const color = getIntegrationColor(integration.type);

    return (
        <div className="bg-white border border-gray-200 rounded-lg p-6 hover:shadow-md transition-shadow">
            <div className="flex items-start justify-between mb-4">
                <div className={`w-12 h-12 bg-${color}-100 rounded-lg flex items-center justify-center`}>
                    <span className={`text-${color}-600 font-bold text-lg uppercase`}>{integration.type.substring(0, 2)}</span>
                </div>
                <div className="flex items-center gap-2">
                    {(integration.type === 'gupshup' || integration.type === 'whatsapp') && (
                        <div
                            className={classNames(
                                'px-2 py-1 rounded-full text-xs font-medium flex items-center gap-1',
                                integration.webhookValidated ? 'bg-blue-100 text-blue-700' : 'bg-yellow-100 text-yellow-700'
                            )}
                            title={
                                integration.webhookValidated
                                    ? `Webhook validated on ${new Date(integration.webhookValidatedAt!).toLocaleString()}`
                                    : 'Webhook not yet validated. Copy and configure the webhook URL in your provider dashboard.'
                            }
                        >
                            {integration.webhookValidated ? '✓ Webhook' : '⚠ Webhook'}
                        </div>
                    )}
                    <div
                        className={classNames(
                            'px-2 py-1 rounded-full text-xs font-medium',
                            integration.isActive ? 'bg-green-100 text-green-700' : 'bg-gray-100 text-gray-700'
                        )}
                    >
                        {integration.isActive ? 'Active' : 'Inactive'}
                    </div>
                </div>
            </div>

            <h3 className="font-semibold text-gray-900 text-lg mb-1">{integration.name}</h3>
            <p className="text-sm text-gray-600 uppercase mb-4">{integration.type}</p>

            <div className="text-xs text-gray-500 mb-4">
                <p>Created: {new Date(integration.createdAt).toLocaleDateString()}</p>
            </div>

            {webhookUrl && (
                <div className="mb-4 bg-gray-50 border border-gray-200 rounded-md p-3">
                    <label className="block text-xs font-medium text-gray-600 mb-1">Webhook URL</label>
                    <div className="flex items-center gap-2">
                        <input
                            type="text"
                            readOnly
                            value={webhookUrl}
                            className="flex-1 px-2 py-1 text-xs bg-white border border-gray-300 rounded font-mono text-gray-700"
                        />
                        <Button type="button" variant="default" size="sm" onClick={handleCopyWebhook}>
                            {copiedWebhook ? 'Copied!' : 'Copy'}
                        </Button>
                    </div>
                </div>
            )}

            <div className="flex space-x-2">
                <Button variant="default" size="sm" onClick={handleTest} className="flex-1">
                    Test
                </Button>
                <Button
                    variant="primary"
                    size="sm"
                    layout="plain"
                    leftIcon={EditIcon}
                    onClick={() => navigate(`/integrations/${integration.id}/edit`)}
                />
                <Button variant="danger" layout="plain" size="sm" leftIcon={TrashIcon} onClick={handleDelete} />
            </div>
        </div>
    );
};
