import React from 'react';
import { Integration } from '../../../types/integration';
import { LoadingSpinner } from '../../components/LoadingSpinner';
import { IntegrationCard } from './IntegrationCard';

interface IntegrationListProps {
    integrations: Integration[];
    isLoading: boolean;
}

export const IntegrationList: React.FC<IntegrationListProps> = ({ integrations, isLoading }) => {
    if (isLoading) {
        return (
            <div className="flex items-center justify-center py-12">
                <LoadingSpinner />
            </div>
        );
    }

    if (integrations.length === 0) {
        return (
            <div className="bg-white rounded-lg border border-gray-200 p-12 text-center">
                <p className="text-gray-500 text-lg">No integrations configured</p>
                <p className="text-gray-400 text-sm mt-2">Add your first integration to start sending campaigns</p>
            </div>
        );
    }

    return (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {integrations.map((integration) => (
                <IntegrationCard key={integration.id} integration={integration} />
            ))}
        </div>
    );
};
