import { Button } from 'ether-ui';
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useIntegrationStore } from '../../services/integration';
import { IntegrationList } from './IntegrationList';

const Integrations: React.FC = () => {
    const navigate = useNavigate();
    const { loadIntegrations, integrations, isLoading } = useIntegrationStore();

    useEffect(() => {
        loadIntegrations();
    }, []);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h2 className="text-2xl font-bold text-gray-900">Integrations</h2>
                    <p className="text-gray-600 mt-1">Connect and manage your marketing channels</p>
                </div>
                <Button onClick={() => navigate('/integrations/create')}>Add Integration</Button>
            </div>

            <IntegrationList integrations={integrations} isLoading={isLoading} />
        </div>
    );
};

export default Integrations;
